import React, { useMemo, useState } from "react";
import PortfolioCard from "../components/PortfolioCard";
import { Animated } from "react-animated-css";
import RazeeGaze from "../assets/images/image2.png";
import IndrasPolymers from "../assets/images/image1.png";

const Portfolio = () => {
  const [selectedCategory, setSelectedCategory] = useState("all");
  const [animate, setAnimate] = useState(true);

  const data = [
    {
      name: "Rajee Gazes",
      image: RazeeGaze,
      category: "development",
    },
    {
      name: "Indras Polymers",
      image: IndrasPolymers,
      category: "development",
    },
    {
      name: "Test",
      image:
        "https://cdn.pixabay.com/photo/2015/04/23/22/00/tree-736885__480.jpg",
      category: "Logo",
    },
    {
      name: "Test",
      image:
        "https://cdn.pixabay.com/photo/2015/04/23/22/00/tree-736885__480.jpg",
      category: "Logo",
    },
    {
      name: "Test",
      image:
        "https://cdn.pixabay.com/photo/2015/04/23/22/00/tree-736885__480.jpg",
      category: "Logo",
    },
  ];

  const categories = data.map((val) => val.category);

  const unique = ["all", ...new Set(categories)];

  useMemo(() => {
    setTimeout(() => {
      setAnimate(true);
    }, 100);
  }, [selectedCategory]);

  const handleSelect = (val) => {
    if (val !== selectedCategory) {
      setAnimate(false);
    }
    setSelectedCategory(val);
  };

  return (
    <main className="page-wrapper">
      <section className="container-fluid py-5 mt-5 px-sm-4 px-md-5">
        <div
          style={{ display: "flex", justifyContent: "center" }}
          className="m-5"
        >
          <h1>Our Portfolio</h1>
        </div>
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            marginTop: "10px",
          }}
        >
          {unique.map((v, i) => {
            return (
              <div
                style={{
                  border:
                    v === selectedCategory
                      ? "1px solid #000000"
                      : "1px solid transparent",
                  padding: "7px 20px",
                  borderRadius: "10px",
                  marginRight: "1%",
                  cursor: "pointer",
                }}
                onClick={() => handleSelect(v)}
              >
                <p
                  style={{
                    textTransform: "capitalize",
                    margin: 0,
                    fontWeight: "500",
                  }}
                >
                  {v}
                </p>
              </div>
            );
          })}
        </div>
        <div className="container mb-5">
          <div className="row">
            {selectedCategory === "all"
              ? data.map((v, i) => {
                  return (
                    <div
                      key={v.name + "_" + i}
                      className="col-lg-4 col-sm-12 mt-5"
                    >
                      <Animated
                        key={i}
                        animationIn="zoomIn"
                        animationOut="zoomOut"
                        animationInDuration={500}
                        animationOutDuration={500}
                        isVisible={animate}
                        animateOnMount={true}
                      >
                        <PortfolioCard
                          key={i}
                          name={v.name}
                          category={v.category}
                          image={v.image}
                        />
                      </Animated>
                    </div>
                  );
                })
              : data.map((v, i) => {
                  return v.category === selectedCategory ? (
                    <div key={i} className="col-lg-4 col-sm-12 mt-5">
                      <Animated
                        key={i}
                        animationIn="zoomIn"
                        animationOut="zoomOut"
                        animationInDuration={500}
                        animationOutDuration={500}
                        isVisible={animate}
                        animateOnMount={true}
                      >
                        <PortfolioCard
                          name={v.name}
                          category={v.category}
                          image={v.image}
                        />
                      </Animated>
                    </div>
                  ) : null;
                })}
          </div>
        </div>
      </section>
    </main>
  );
};

export default Portfolio;
